.hero{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.card{
    padding-left: 40px;
    padding-right: 40px;
    text-align: center;
    border-radius: 5px;
    background: rgba(240, 248, 255, 0.241);
}
img{
    width: 120px;
    height: 200px;
    margin: 20px;
    border-radius: 15px;
}
h2{
    color:white;
    text-shadow: 2px 2px rgba(240, 248, 255, 0.241) ;
}
a{
    color: #0b0089;
    text-decoration: none;
}